body {
  font-family: "whitney ssm a", "whitney ssm b", verdana, sans-serif;
  background: #f8f7f8;
  color: #444;
  line-height: 1.5; }
  body.has-modal {
    overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

p {
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 20px;
  line-height: 1.8; }

a {
  color: #18bcd7; }
  a:hover {
    text-decoration: none; }
  a[href^="tel:"] {
    white-space: nowrap; }
    @media (min-width: 768px) {
      a[href^="tel:"] {
        color: inherit;
        text-decoration: none; } }

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none; }

h3 {
  font-size: 16px;
  margin-bottom: 5px; }

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  max-width: 930px;
  width: 100%; }
  @media (min-width: 768px) {
    .container {
      padding-left: 30px;
      padding-right: 30px; } }

.button {
  display: inline-block;
  white-space: nowrap;
  border: 0;
  border-radius: 5px;
  color: #666;
  background-color: gray;
  padding: 5px 15px;
  padding-bottom: 4px;
  text-decoration: none;
  text-align: center;
  border: 1px solid transparent;
  transition: all .2s; }

.button--primary {
  background-color: #0d395f;
  color: #fff; }
  .button--primary:hover, .button--primary:focus {
    background-color: #0a2c49; }

.button--secondary {
  background-color: #18bcd7;
  color: #fff; }
  .button--secondary:hover, .button--secondary:focus {
    background-color: #15a8c0; }
  .button--secondary.button--outline {
    border-color: #18bcd7;
    background-color: transparent;
    color: #18bcd7; }
    .button--secondary.button--outline:hover {
      background-color: #18bcd7;
      color: #fff; }

.button--accent {
  background-color: #c26533;
  color: #fff; }
  .button--accent:hover, .button--accent:focus {
    background-color: #ae5a2e; }

.button--block {
  display: block;
  width: 100%; }

.site-header {
  border-top: 5px solid #18bcd7; }
  .js .site-header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 5; }

.site-header__title {
  background-color: #0d395f;
  padding-top: 10px;
  padding-bottom: 10px; }
  .home .site-header__title {
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden; }

.site-title {
  font-weight: 400;
  font-size: 18px;
  margin: 0; }
  .site-title a {
    color: #fff;
    text-decoration: none; }

.footer {
  border-top: 2px solid #0d395f; }
  .home .footer {
    border-top: 5px solid #18bcd7; }

.footer__logo {
  padding-top: 20px; }

.site-nav {
  padding: 10px 0;
  border-bottom: 1px solid #18bcd7;
  background-color: #f8f7f8; }
  .home .site-nav {
    background-color: #0d395f; }

.site-nav__items {
  display: flex;
  align-items: center; }

.site-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex: 1; }

.site-nav__list-item:not(:last-of-type) {
  border-right: 1px solid #18bcd7; }

.site-nav__link {
  display: inline-block;
  padding-left: 15px;
  padding-right: 15px;
  text-decoration: none; }
  .site-nav__link:hover {
    text-decoration: underline; }
  .site-nav__list-item:first-child .site-nav__link {
    padding-left: 0; }
  .site-nav__link.active {
    font-weight: bold; }
    body:not(.home) .site-nav__link.active {
      color: #0d395f; }

.site-nav__button {
  flex-basis: auto;
  align-self: flex-end; }

.resource {
  position: relative;
  padding-top: 10px;
  margin-bottom: 60px; }
  @media (min-width: 768px) {
    .resource {
      max-width: 510px; } }
  .resource:before {
    content: '';
    display: block;
    background-color: #18bcd7;
    width: 50px;
    height: 5px;
    position: absolute;
    top: 0;
    left: 0; }

.resource__title {
  font-size: 16px;
  margin-bottom: 5px; }

.resource__description p {
  margin-bottom: 0; }

.resource__description p + p {
  margin-top: 1rem; }

.resource__note p {
  font-size: 14px; }

.resource__url {
  margin-bottom: 0; }

.resource__email {
  margin-bottom: 0;
  font-size: 14px; }

.resource__address p {
  font-size: 14px;
  margin-bottom: 0; }

.resource__tel {
  font-size: 14px;
  margin-bottom: 0;
  line-height: 1.8; }

.resource__confidentiality {
  font-size: 14px;
  margin-bottom: 0; }

.resource__person-list {
  margin-top: 40px; }

.person {
  margin-bottom: 20px; }
  .person p {
    font-size: 14px; }

.person__name {
  font-size: 14px;
  margin-bottom: 0; }

.person__title {
  margin-bottom: 0; }

.person__address {
  margin-bottom: 0; }
  .person__address p {
    margin-bottom: 0; }

.person__email {
  margin-bottom: 0; }

.person__tel {
  margin-bottom: 0; }

.hero {
  background-color: #0d395f;
  text-align: center;
  height: 80vh;
  display: flex;
  justify-content: center; }
  @media (min-width: 768px) {
    .hero {
      display: block;
      text-align: left; } }

.hero__content {
  align-self: center; }
  @media (min-width: 768px) {
    .hero__content {
      padding-top: 100px;
      max-width: 930px;
      padding-left: 30px;
      padding-right: 30px;
      margin: 0 auto; } }

.hero__title {
  color: #fff;
  margin-top: 0;
  font-size: 50px;
  font-weight: 300;
  margin-bottom: 0;
  line-height: 1; }
  @media (min-width: 768px) {
    .hero__title {
      font-size: 85px;
      margin-left: -17px;
      margin-bottom: 5px; } }

.hero__text {
  color: #fff;
  font-size: 18px; }
  @media (min-width: 768px) {
    .hero__text {
      font-size: 24px; } }

.hero__button {
  width: 100%;
  margin-top: 100px;
  font-weight: bold;
  padding-top: 10px;
  padding-bottom: 10px; }
  @media (min-width: 768px) {
    .hero__button {
      padding-left: 100px;
      padding-right: 100px;
      width: auto; } }

.home .main .container {
  max-width: 100%;
  padding: 0; }

.modal {
  background-color: #fff;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
  box-shadow: 5px 0 10px rgba(0, 0, 0, 0.1);
  display: none; }

.modal--is-open {
  display: block; }

.modal--is-top {
  z-index: 3; }

.modal__main {
  height: 100%;
  padding-bottom: 50px; }

.modal__controls {
  background-color: #18bcd7;
  padding-top: 10px;
  padding-bottom: 10px; }

.modal__button {
  display: block;
  color: #fff;
  font-size: 18px;
  width: 100%;
  text-decoration: none; }

.modal__button:hover .modal__button-text {
  text-decoration: underline; }

.modal__container {
  height: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch; }

.markdown ul {
  list-style: initial; }

.markdown ol {
  list-style: initial; }

.markdown ul,
.markdown ol {
  margin-bottom: 20px;
  padding-left: 20px; }

.markdown h3 {
  font-size: 16px;
  margin-bottom: 10px;
  line-height: 1.5; }

.discovery__back-button {
  margin-top: 20px; }
